import React, {PureComponent} from 'react'
import {
  Row,
  Col,
  Button,
  message,
  Spin
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {hasVariant, getVariant, removeVariant} from 'Variant'
import JsBarcode from 'jsbarcode'
const ORDER_LIST = 'wms/productStorageRelation'

@connect(
  state => ({...state.productStorageRelation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '序号',
      dataIndex: 'index',
      key: 'index',
      align: 'center',
      render: (text, record, index) => index + 1
    }, {
      title: '商品编码',
      dataIndex: 'productCode',
      key: 'productCode',
      align: 'center'
    }, {
      title: '商品条码',
      dataIndex: 'productBarCode',
      key: 'productBarCode',
      align: 'center'
    }, {
      title: '商品名称',
      dataIndex: 'productName',
      key: 'productName',
      align: 'center'
    }, {
      title: '单价（元）',
      dataIndex: 'price',
      key: 'price',
      align: 'center'
    }, {
      title: '收货单位',
      dataIndex: 'unit',
      key: 'unit',
      align: 'center'
    }, {
      title: '计划量',
      dataIndex: 'planQty',
      key: 'planQty',
      align: 'center'
    }, {
      title: '已收量',
      dataIndex: 'receiveQty',
      key: 'receiveQty',
      align: 'center'
    }, {
      title: '金额总计（元）',
      dataIndex: 'amount',
      key: 'amount',
      align: 'center'
    }]
  }
  componentDidMount() {
    this._getAsyncData()
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData() {
    try {
      let ids = [],
        reqData = []
      if (hasVariant(ORDER_LIST)) {
        ids = getVariant(ORDER_LIST)
      } else {
        removeVariant(ORDER_LIST)
      }
      if (!ids.length) return (0, this._closeWindow)(message.error('订单编号错误'))
      console.log(this.props)
      ids.map((o) => {
        reqData.push({
          id: o
        })
      })
      const {handleGetDetailPrint} = this.props
      handleGetDetailPrint({data: reqData})
    } catch (err) {
      console.error(err)
    }
  }
  /* 关闭窗口 */
  _closeWindow = () => window.setTimeout(() => window.close(), 2000)
  /* 打印 */
  handlePrint = () => {
    if (window.print) {
      window.print()
    } else {
      document.execCommand('print')
    }
  }

  // 画条形码
  componentDidUpdate() {
    const {printData} = this.props
    printData.map((o) => {
      JsBarcode(`#key${o.storageLocationCode}`, o.storageLocationCode, {
        width: 2.5,
        height: 60,
        displayValue: false
      })
    })
  }
  render() {
    const {printData, printLoading} = this.props
    const printLoadingSty = {
      position: 'absolute',
      left: '50%',
      top: '50%',
      marginLeft: -20,
      marginTop: -20
    }
    return (
      <React.Fragment>
        {
          printData.map((o, i) => <div className="productStorageRelation" key={o.storageLocationCode}>
                    <h2 style={{fontWeight: 'bolder', fontSize: '36px', marginBottom: '10px'}}>{o.productName}</h2>
                    <div style={{width: '90%', margin: 'auto', borderTop: '1px solid #000', borderBottom: '1px solid #000', padding: '15px 0px', textAlign: 'center', fontSize: '20px'}}>
                      <h2 style={{fontWeight: 'bolder', fontSize: '20px', margin: 0}}>商品条码：{o.productBarCode} 商品编码：{o.productCode}</h2>
                    </div>
                    <h3 style={{fontWeight: 'bolder', fontSize: '48px', marginBottom: '10px'}}>{o.assembleStorageLocationCode}</h3>
                    <img id={`key${o.storageLocationCode}`}/>
                  </div>)
        }
        {
          printData.length ? <Button className="noprint" type={'primary'} onClick={this.handlePrint}>打印</Button> : (printLoading ? <h2 style={{textAlign: 'center'}}>正在加载打印数据</h2> : <h2 style={{textAlign: 'center'}}>暂无打印数据</h2>)
        }
        <div style={printLoadingSty}>
          <Spin spinning={printLoading} size="large"/>
        </div>
      </React.Fragment>
    )
  }
}



